'han-etal-2019-deep':
`@inproceedings{han-etal-2019-deep,
    title = "Deep Structured Neural Network for Event Temporal Relation Extraction",
    author = "Han, Rujun  and
      Hsu, I-Hung  and
      Yang, Mu  and
      Galstyan, Aram  and
      Weischedel, Ralph  and
      Peng, Nanyun",
    booktitle = "Proceedings of the 23rd Conference on Computational Natural Language Learning (CoNLL)",
    month = "nov",
    year = "2019",
    address = "Hong Kong, China",
    publisher = "Association for Computational Linguistics",
    url = "https://www.aclweb.org/anthology/K19-1062",
    doi = "10.18653/v1/K19-1062",
    pages = "666--106",
    abstract = "We propose a novel deep structured learning framework for event temporal relation extraction. The model consists of 1) a recurrent neural network (RNN) to learn scoring functions for pair-wise relations, and 2) a structured support vector machine (SSVM) to make joint predictions. The neural network automatically learns representations that account for long-term contexts to provide robust features for the structured model, while the SSVM incorporates domain knowledge such as transitive closure of temporal relations as constraints to make better globally consistent decisions. By jointly training the two components, our model combines the benefits of both data-driven learning and knowledge exploitation. Experimental results on three high-quality event temporal relation datasets (TCR, MATRES, and TB-Dense) demonstrate that incorporated with pre-trained contextualized embeddings, the proposed model achieves significantly better performances than the state-of-the-art methods on all three datasets. We also provide thorough ablation studies to investigate our model.",
}
`,

'han-etal-2019-joint':
`@inproceedings{han-etal-2019-joint,
    title = "Joint Event and Temporal Relation Extraction with Shared Representations and Structured Prediction",
    author = "Han, Rujun  and
      Ning, Qiang  and
      Peng, Nanyun",
    booktitle = "Proceedings of the 2019 Conference on Empirical Methods in Natural Language Processing and the 9th International Joint Conference on Natural Language Processing (EMNLP-IJCNLP)",
    month = "nov",
    year = "2019",
    address = "Hong Kong, China",
    publisher = "Association for Computational Linguistics",
    url = "https://www.aclweb.org/anthology/D19-1041",
    doi = "10.18653/v1/D19-1041",
    pages = "434--444",
    abstract = "We propose a joint event and temporal relation extraction model with shared representation learning and structured prediction. The proposed method has two advantages over existing work. First, it improves event representation by allowing the event and relation modules to share the same contextualized embeddings and neural representation learner. Second, it avoids error propagation in the conventional pipeline systems by leveraging structured inference and learning methods to assign both the event labels and the temporal relation labels jointly. Experiments show that the proposed method can improve both event extraction and temporal relation extraction over state-of-the-art systems, with the end-to-end F1 improved by 10% and 6.8% on two benchmark datasets respectively.",
}
`,

'han-etal-2018-conditional':
`@inproceedings{han-etal-2018-conditional,
    title = "Conditional Word Embedding and Hypothesis Testing via {B}ayes-by-Backprop",
    author = "Han, Rujun  and
      Gill, Michael  and
      Spirling, Arthur  and
      Cho, Kyunghyun",
    booktitle = "Proceedings of the 2018 Conference on Empirical Methods in Natural Language Processing",
    month = "oct",
    year = "2018",
    address = "Brussels, Belgium",
    publisher = "Association for Computational Linguistics",
    url = "https://www.aclweb.org/anthology/D18-1527",
    doi = "10.18653/v1/D18-1527",
    pages = "4890--4895",
    abstract = "Conventional word embedding models do not leverage information from document meta-data, and they do not model uncertainty. We address these concerns with a model that incorporates document covariates to estimate conditional word embedding distributions. Our model allows for (a) hypothesis tests about the meanings of terms, (b) assessments as to whether a word is near or far from another conditioned on different covariate values, and (c) assessments as to whether estimated differences are statistically significant.",
}
`,

