'muric2015':
`@inproceedings{Muric2015,
  abstract = {Can linear time-invariant dynamics be used to model the epidemics on the networks? This paper shows that this is indeed possible. Given the topology of a network in terms of an undirected graph we form a state space representation of a linear system to study the behavior of this network and to compare calculations against simulations of epidemics. In particular, an epidemic modeling approach based on systems theory usable even for lager networks is introduced and its potential is demonstrated. Also, methods to form the state variables of a corresponding LTI system are proposed. Presented results show that this approach is highly effective to evaluate epidemic dynamics analytically in every discrete time step omitting agent-based simulations. Moreover, it is shown that it can be used for network analysis and network optimization against virus spreading. This opens the door for using systems theory tools in network analysis.},
  address = {Abu Dhabi},
  author = {Muric, G. and Scheunert, C. and Jorswieck, E.A.},
  booktitle = {The IEEE WiMob 2015 Workshop on Emergency Networks for Public Protection and Disaster Relief},
  doi = {10.13140/RG.2.1.4520.9043},
  isbn = {9781467377010},
  pages = {138--146},
  title = {{On modeling epidemics in networks using linear time-invariant dynamics}},
  year = {2015}
}
`,

'muric2016':
`@article{Muric2016,
  abstract = {Networks are used for modeling numerous technical, social or biological systems. In order to better understand the system dynamics, it is a matter of great interest to identify the most important nodes within the network. For a large set of problems, whether it is the optimal use of available resources, spreading information efficiently or even protection from malicious attacks, the most important node is the most influential spreader, the one that is capable of propagating information in the shortest time to a large portion of the network. Here we propose the Node Imposed Response (NiR), a measure which accurately evaluates node spreading power. It outperforms betweenness, degree, k-shell and h-index centrality in many cases and shows the similar accuracy to dynamics-sensitive centrality. We utilize the system-theoretic approach considering the network as a Linear Time-Invariant system. By observing the system response we can quantify the importance of each node. In addition, our study provides a robust tool set for various protective strategies.},
  author = {Muric, Goran and Jorswieck, Eduard and Scheunert, Christian},
  doi = {10.1371/journal.pone.0168514},
  editor = {Csermely, Peter},
  issn = {19326203},
  journal = {PLOS ONE},
  month = {dec},
  number = {12},
  pages = {e0168514},
  publisher = {Springer-Verlag},
  title = {{Using LTI Dynamics to Identify the Influential Nodes in a Network}},
  url = {http://dx.plos.org/10.1371/journal.pone.0168514},
  volume = {11},
  year = {2016}
}
`,

