'10.1007/978-3-030-24209-1_1':
`@inproceedings{10.1007/978-3-030-24209-1_1,
  abstract = {Simulating and predicting planetary-scale techno-social systems poses heavy computational and modeling challenges. The DARPA SocialSim program set the challenge to model the evolution of GitHub, a large collaborative software-development ecosystem, using massive multi-agent simulations. We describe our best performing models and our agent-based simulation framework, which we are currently extending to allow simulating other planetary-scale techno-social systems. The challenge problem measured participant's ability, given 30 months of meta-data on user activity on GitHub, to predict the next months' activity as measured by a broad range of metrics applied to ground truth, using agent-based simulation. The challenge required scaling to a simulation of roughly 3 million agents producing a combined 30 million actions, acting on 6 million repositories with commodity hardware. It was also important to use the data optimally to predict the agent's next moves. We describe the agent framework and the data analysis employed by one of the winning teams in the challenge. Six different agent models were tested based on a variety of machine learning and statistical methods. While no single method proved the most accurate on every metric, the broadly most successful sampled from a stationary probability distribution of actions and repositories for each agent. Two reasons for the success of these agents were their use of a distinct characterization of each agent, and that GitHub users change their behavior relatively slowly.},
  address = {Cham},
  author = {Blythe, Jim and Bollenbacher, John and Huang, Di and Hui, Pik-Mai and Krohn, Rachel and Pacheco, Diogo and Muric, Goran and Sapienza, Anna and Tregubov, Alexey and Ahn, Yong-Yeol and Flammini, Alessandro and Lerman, Kristina and Menczer, Filippo and Weninger, Tim and Ferrara, Emilio},
  booktitle = {Advances in Practical Applications of Survivable Agents and Multi-Agent Systems: The PAAMS Collection},
  editor = {Demazeau, Yves and Matson, Eric and Corchado, Juan Manuel and la Prieta, Fernando},
  isbn = {978-3-030-24209-1},
  pages = {3--15},
  publisher = {Springer International Publishing},
  title = {{Massive Multi-agent Data-Driven Simulations of the GitHub Ecosystem}},
  year = {2019}
}
`,

'Ozer2019':
`@article{Ozer2019,
  abstract = {How is popularity gained online? Is being successful strictly related to rapidly becoming viral in an online platform or is it possible to acquire popularity in a steady and disciplined fashion? What are other temporal characteristics that can unveil the popularity of online content? To answer these questions, we leverage a multi-faceted temporal analysis of the evolution of popular online contents. Here, we present dipm-SC: a multi-dimensional shape-based time-series clustering algorithm with a heuristic to find the optimal number of clusters. First, we validate the accuracy of our algorithm on synthetic datasets generated from benchmark time series models. Second, we show that dipm-SC can uncover meaningful clusters of popularity behaviors in a real-world Twitter dataset. By clustering the multidimensional time-series of the popularity of contents coupled with other domain-specific dimensions, we uncover two main patterns of popularity: bursty and steady temporal behaviors. Moreover, we find that the way popularity is gained over time has no significant impact on the final cumulative popularity.},
  archivePrefix = {arXiv},
  arxivId = {1904.04994},
  author = {Ozer, Mert and Sapienza, Anna and Abeliuk, Andres and Muric, Goran and Ferrara, Emilio},
  eprint = {1904.04994},
  journal = {arXiv Preprint},
  month = {apr},
  title = {{Discovering patterns of online popularity from time series}},
  url = {http://arxiv.org/abs/1904.04994},
  year = {2019}
}
`,

